import { PickContextProvider, PickContext } from "../context/PickContext"
import { useContext, useEffect } from "react"
import questions from "../util/questions"
import { configurationData, chartData, configurationKeys } from "../util/data"
import { Bar } from "react-chartjs-2"

const Section = ({ id, title, children, autoslide = 0 }) => {
  const props = {
    id,
    "data-transition": "slide",
  }
  if (autoslide > 0) {
    props["data-autoslide"] = autoslide
  }
  return (
    <section {...props}>
      <h3>{title}</h3>
      <div class="choices">{children}</div>
    </section>
  )
}

const Button = ({
  title,
  link,
  questionKey,
  buttonType = "pick",
  onClick = () => null,
}) => {
  const [configurations, setConfigurations] = useContext(PickContext)
  const [linkKey, questionValue] = link.split("#")
  const setSelection = () => {
    if (questionKey) {
      const updated = {
        ...configurations,
        selections: {
          ...configurations.selections,
          [configurations.selections.current]: {
            ...configurations.selections[configurations.selections.current],
            [questionKey]: questionValue,
          },
        },
      }
      setConfigurations(updated)
    }
    onClick()
  }

  const disabled = buttonType.includes("disabled")

  return disabled ? (
    <a // eslint-disable-line jsx-a11y/anchor-is-valid
      className={buttonType}
      title="Option unavailable for selected training configuration"
    >
      {title}
    </a>
  ) : (
    <a href={`#/${linkKey}`} className={buttonType} onClick={setSelection}>
      {title}
    </a>
  )
}

const Question = ({ config }) => {
  const [configurations] = useContext(PickContext)
  const currentConfig =
    configurations.selections[configurations.selections.current]
  const linksKey =
    currentConfig[config.linkKey] in config.links
      ? currentConfig[config.linkKey]
      : "default"
  const buttonClass = (x) => {
    if ("enabledFor" in x) {
      const enabled = x.enabledFor.reduce((all, e) => {
        const [k, v] = e.split("#")
        return all || currentConfig[k] === v
      }, false)
      return enabled ? "pick" : "disabled pick"
    } else return "pick"
  }
  return (
    <Section id={config.id} title={config.title}>
      {config.back ? (
        <Button link={config.back} buttonType="round back"></Button>
      ) : null}
      {(config.links[linksKey] || []).map((x) => {
        return (
          <Button
            key={x.link}
            {...x}
            questionKey={config.id}
            buttonType={buttonClass(x)}
          />
        )
      })}
    </Section>
  )
}

const readyToGenerateChartData = (config) =>
  config &&
  config.flavor &&
  config.dataset &&
  config.architecture &&
  config.train &&
  !("data" in config)

const Training = () => {
  const [configurations, setConfigurations] = useContext(PickContext)

  useEffect(() => {
    const getChartData = async () => {
      let updated = false
      const newConfigurations = { ...configurations }
      for (let index in configurationKeys(configurations.selections)) {
        if (readyToGenerateChartData(newConfigurations.selections[index])) {
          const keyAndData = await configurationData(
            newConfigurations.selections[index],
            index
          )
          newConfigurations.selections[index] = {
            ...newConfigurations.selections[index],
            ...keyAndData,
          }
          updated = true
        }
      }
      if (updated) setConfigurations(newConfigurations)
    }
    if (configurations) getChartData()
  }, [configurations, setConfigurations])
  const currentConfig =
    configurations.selections[configurations.selections.current]
  const title = `${currentConfig.train} Training In Progress...`
  return (
    <Section id="training" title={title} autoslide={3000}>
      <div className="loading"></div>
    </Section>
  )
}

const ResultSection = ({ chartData, refreshToken }) => {
  const chartHeight = 350
  return (
    <div style={{ flex: 1, height: chartHeight, minHeight: chartHeight }}>
      <Bar
        key={refreshToken}
        data={chartData || {}}
        options={{
          maintainAspectRatio: false,
          responsive: true,
          scales: {
            yAxes: [
              {
                ticks: {
                  max: 1,
                  min: 0,
                },
              },
            ],
          },
        }}
      />
    </div>
  )
}

const Results = () => {
  const [configurations, setConfigurations] = useContext(PickContext)
  const title =
    configurations.selections.current > 0
      ? "Compare Training Results"
      : "Training Results"

  const addComparison = () => {
    const nextId = configurations.selections.current + 1
    const compareTo =
      configurations.selections[configurations.selections.current]
    setConfigurations({
      ...configurations,
      selections: {
        ...configurations.selections,
        current: nextId,
        [nextId]: { flavor: compareTo.flavor, dataset: compareTo.dataset },
      },
    })
  }

  const refreshToken = Date.now()
  const resetComparisons = () =>
    setConfigurations({ selections: { current: 0, 0: {} } })

  const data = chartData(configurations.selections)

  return (
    <Section id="results" title={title}>
      <div style={{ display: "block", minHeight: 360, height: 360 }}>
        <div
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <ResultSection chartData={data} refreshToken={refreshToken} />
        </div>
      </div>
      <div className="loading-options">
        <Button
          title="Start Over"
          link="flavor"
          buttonType="back pick"
          onClick={resetComparisons}
        />
        {data && data.datasets && data.datasets.length < 4 ? (
          <Button title="Compare" link="architecture" onClick={addComparison} />
        ) : null}
      </div>
      <br />
    </Section>
  )
}

const Pick = () => {
  return (
    <PickContextProvider>
      {Object.keys(questions).map((x) => (
        <Question key={x} config={questions[x]} />
      ))}
      <Training />
      <Results />
    </PickContextProvider>
  )
}

export default Pick
